import React from 'react';
import {render} from 'react-dom';
import './index.css';
import {HashRouter, Switch, Route} from 'react-router-dom';
import App from './App';

import {Provider} from 'react-redux'
import store from './store'


//=>css
import './static/css/common.sass'
import ProjectConfig from "./routes/ProjectConfig";
import Login from "./routes/Login";
import DatabaseManager from "./routes/DatabaseManager";


render(<Provider store={store}>
    <HashRouter>
        <Switch>
            <Route path='/' exact component={App}/>
            <Route path='/admin' component={App}/>
            <Route path='/projectConfig' component={ProjectConfig} />
            <Route path='/databaseManager' component={DatabaseManager} />
            <Route path='/login' component={Login} />

        </Switch>

    </HashRouter>
</Provider>, document.getElementById('root'));
