import * as TYPE from './../action-types'

let stateDefault = {
    projectTree:[]
};


export default function projectConfig(state=stateDefault, action) {
    state = JSON.parse(JSON.stringify(state));

    switch(action.type) {

        case TYPE.CONFIG_PROJECT_FETCH_TREE:
            state.projectTree= action.result.data.data
            break;

            /**
        case TYPE.SHOW_PROJECT_DETAIL:
            state.showDetail = action.result.data
            break;
             */

    }

    return state
}
