import * as TYPES from "../action-types"
import { fetchProjectFileTreeApi } from '../../api/projectConfig'


let projectConfig = {

    fetchProjectFileTree(projectId, packageType, moduleId) {
        return async dispatch=>{
            let result = await fetchProjectFileTreeApi(projectId, packageType, moduleId)
            dispatch({
                type:TYPES.CONFIG_PROJECT_FETCH_TREE,
                result
            })
        }
    },


}

export default projectConfig