export function getVoPrefixAndPostfix(resObj) {

    var prefix = '';
    var postfix = '';

    if(resObj.collectionType == 'list') { // single list map
        prefix = 'List<';
        postfix = '>';
    } else if(resObj.collectionType == 'map') { // single list map
        prefix = 'Map<String, ';
        postfix = '>';
    } else if(resObj.collectionType == 'set') { // single list map
        prefix = 'Set<';
        postfix = '>';
    }
    return {prefix, postfix};
}