import * as TYPE from './../action-types'

let stateDefault = {
    fileInfo: {},
    fileFunctions: []
};


export default function fetchFileInfo(state=stateDefault, action) {
    state = JSON.parse(JSON.stringify(state));

    switch(action.type) {

        case TYPE.CONFIG_PROJECT_FETCH_FILE_INFO:
            state.fileInfo= action.result.data.data
            break;


         case TYPE.CONFIG_PROJECT_FETCH_FILE_FUNS:
         state.fileFunctions = action.result.data.data
         break;


    }

    return state
}
